.profile__name {
  margin: 0;
  padding: 0;
  max-width: 293px;
  font-style: normal;
  font-weight: 500;
  font-size: 42px;
  line-height: 48px;
  color: var(--light);
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media screen and (max-width: 618px) {
  .profile__name {
    max-width: 190px;
    padding-left: 25px;
    font-size: 27px;
    line-height: 33px;
  }
}
